import { _decorator, Component, Node, VerticalTextAlignment, Label, Vec3, Color } from 'cc';
const { ccclass, property } = _decorator;
import { SudokuCreator } from '@algorithm.ts/sudoku';

@ccclass('GameController')
export class GameController extends Component {
    @property(Node)
    boardNode: Node;
    @property(Label)
    numberLabels: Label[] = [];
    private selectedNode: Node = null;
    puzzleGame: number[] = [];
    solutionGame: number[] = [];


    onLoad() {
        this.createPuzzleBoard();
    }

    

    createPuzzleBoard(){
        let sudokuCreator = new SudokuCreator({childMatrixWidth: 3});
        let boardData = sudokuCreator.createSudoku(0.2);

        for (let i = 0; i < boardData.puzzle.length; i++) {
            this.puzzleGame.push(boardData.puzzle[i] + 1);
        }
        for (let i = 0; i < boardData.solution.length; i++) {
            this.solutionGame.push(boardData.solution[i] + 1);
        }

        for (let i = 0; i < 9; i++) {
            for (let j = 0; j < 9; j++) {
                const value = this.puzzleGame[i*9 + j];
                const nodeSudoku = new Node("ValueGame");
                nodeSudoku.setPosition(i * 55, j * 55);
                this.boardNode.addChild(nodeSudoku);
                
                const labelBlock = nodeSudoku.addComponent(Label);
                labelBlock.string = value === 0 ? "-" : value.toString();

                nodeSudoku.on(Node.EventType.TOUCH_END, () => {
                    this.selectedNode = nodeSudoku;
                });
            }
            const nodeNum = new Node("NumberValueLabel");
            const label = nodeNum.addComponent(Label);
            label.string = `${i + 1}`;
            label.verticalAlign = VerticalTextAlignment.CENTER;
            nodeNum.parent = this.node;
            nodeNum.position = new Vec3(i % 3 * 100 + 180, Math.floor(i / 3) * -110, 0);
            this.numberLabels.push(label);
            nodeNum.on(Node.EventType.TOUCH_END, () => {
                if (this.selectedNode) {
                    // const labelBlock = this.selectedNode.getComponent(Label);
                    // labelBlock.string = label.string;
                    // labelBlock.color = Color.BLACK;

                    const selectedValue = parseInt(label.string); // Convert the label's string value to a number
                    if (!isNaN(selectedValue)) { // Check if the selected value is a valid number
                        const i = Math.floor(this.selectedNode.position.x / 55); // Calculate the i index based on the selected node's position
                        const j = Math.floor(this.selectedNode.position.y / 55); // Calculate the j index based on the selected node's position
                        const position = i * 9 + j; // Calculate the position in the array based on the i and j indices
                        const solutionValue = this.solutionGame[position];
                        const isCorrect = selectedValue === solutionValue;
                        console.log(isCorrect); // Display true or false based on the comparison

                        // Change the color of the label to green if the selected value is correct, or red otherwise
                        const labelBlock = this.selectedNode.getComponent(Label);
                        labelBlock.color = isCorrect ? Color.GREEN : Color.RED;
                        labelBlock.string = selectedValue.toString();
                    }
                }
            });
        }
        
    }   
}

